@import url("https://fonts.googleapis.com/css2?family=Josefin+Sans&display=swap");

*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
input {
  font-size: 1.1rem;
  font-family: "Josefin Sans", sans-serif;
  color: white;
  &::placeholder {
    color: #f2f2f2;
  }
}
html {
  font-size: 16px;
}
body {
  height: 100vh;
  background: linear-gradient(to bottom, $dark-gray-blue, $dark-very-blue 20%);
  display: flex;
  justify-content: center;
}
body {
  box-sizing: border-box;
}
#root {
  font-size: 18px;
  font-family: "Josefin Sans", sans-serif;
  width: 33%;
  min-width: 450px;
  margin-top: 2rem;
  display: flex;
  flex-direction: column;
}
ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.bright-blue {
  background-color: hsl(220, 98%, 61%);
}
.light {
  background-color: hsl(0, 0%, 98%);
}
.header {
  &-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 3rem 0;
    color: white;
    margin: none;
    letter-spacing: 1rem;
  }
}
.form {
  width: 100%;
  display: flex;
  flex-direction: row-reverse;
  align-items: center;
  padding-left: 10px;
  background-color: $dark-desaturated-blue;
  border-radius: 5px;
  &-input {
    width: 100%;
    background-color: transparent;
    color: white;
    outline: none;
    padding: 1rem;

    border: none;
  }
  &-button {
    background-color: transparent;

    outline: none;
    border: 1px solid white;
    border-radius: 50%;
    width: 25px;
    height: 25px;
    cursor: pointer;
  }
}
.todo-item {
  display: flex;
  justify-content: space-between;
  background-color: $dark-desaturated-blue;
  border-bottom: 1px solid white;
  padding-left: 10px;
  padding-right: 10px;
  align-items: center;
  color: hsl(0, 0%, 98%);
  & li {
    padding: 1rem;
    justify-self: flex-start;
  }
}
.circle-button {
  background-color: transparent;
  outline: none;
  border: 1px solid white;
  border-radius: 50%;
  width: 25px;
  height: 25px;
  cursor: pointer;
}
.ul-container {
  display: flex;
  flex-direction: column;
  border-radius: 5px;
  margin-top: 1rem;
}
.bgcolorbutton {
  background: linear-gradient(
    to bottom,
    hsl(192, 100%, 67%),
    hsl(280, 87%, 65%)
  );
}
.delete {
  background-color: transparent;
  cursor: pointer;
  outline: none;
  border: none;
  & svg {
    width: 100%;
    color: white;
  }
}
.btn-theme {
  background-color: transparent;
  cursor: pointer;
  outline: none;
  border: none;
  & svg {
    width: 100%;
    color: white;
  }
}
.todo-footer {
  padding: 1rem;
  &__button {
    cursor: pointer;
    outline: none;
    border: none;
    padding: 10px;
    background-color: transparent;
    color: hsl(0, 0%, 98%);
    font-size: 0.8rem;
  }
}
.choosen {
  color: hsl(220, 98%, 61%);
}
